import React from 'react';
import { NavLink } from "react-router-dom";

function Nav() {
    return (
        <div className='header'>
        <NavLink 
            to='/website' 
            exact 
            activeStyle={{
              fontWeight: "bold",
              color: "red"
            }}
        >
            Home
        </NavLink>
        <NavLink
            to='/about'
            exact
            activeStyle={{
              fontWeight: "bold",
              color: "red"
            }}>
            About
        </NavLink>
        </div>
      );
    };


export default Nav;